<template>
  <div class="addproject">
    <div class="back">
      <el-button @click="goback" size="small" type="primary"></el-button>
    </div>
    <div class="addcontent" :style="theight">
      <el-row class="box">
        <fieldset>
          <legend></legend>
          <div class="boxcontent basicbox">
            <el-form
              label-width="110px"
              size="medium"
              ref="basicInfo"
              :rules="rules"
              :inline="true"
              :model="basicInfo"
            >
              <div class="blist">
                <el-form-item label="" prop="name">
                  <el-input placeholder="" v-model="basicInfo.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="owndeptid">
                  <el-select v-model="basicInfo.owndeptid" filterable clearable placeholder="">
                    <el-option
                      v-for="item in leadunits"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </div>
              <div class="blist">
                <el-form-item label="" prop="arealeadids" class="ww1">
                  <el-select
                    v-model="basicInfo.arealeadids"
                    collapse-tags
                    multiple
                    clearable
                    placeholder=""
                  >
                    <el-option
                      v-for="item in leaderUsers"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="street" class="ww1">
                  <el-select
                    v-model="basicInfo.street"
                    filterable
                    multiple
                    collapse-tags
                    clearable
                    placeholder=""
                  >
                    <el-option
                      v-for="item in streetdatas"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </div>
              <div class="blist">
                <el-form-item label="" prop="submitres" class="bsrequire">
                  <el-input
                    type="textarea"
                    placeholder=""
                    :rows="3"
                    v-model="basicInfo.submitres"
                  ></el-input>
                </el-form-item>
                <el-form-item label="" prop="remark" class="bsrequire">
                  <el-input type="textarea" placeholder="" :rows="3" v-model="basicInfo.remark"></el-input>
                </el-form-item>
              </div>
              <div class="blist">
                <el-form-item label="" prop="category">
                  <el-radio @change="selectChange" v-model="basicInfo.category" label="0"></el-radio>
                  <el-radio @change="selectChange" v-model="basicInfo.category" label="1"></el-radio>
                </el-form-item>
                <el-form-item label="" prop="address">
                  <el-input placeholder="" v-model="basicInfo.address"></el-input>
                </el-form-item>
              </div>
              <div class="blist">
                <el-form-item label="" prop="mapitude">
                  <el-input placeholder="" v-model="basicInfo.mapitude"></el-input>
                </el-form-item>
                <el-form-item label="" prop="aimplan">
                  <el-input type="textarea" placeholder="" :rows="2" v-model="basicInfo.aimplan"></el-input>
                </el-form-item>
              </div>
            </el-form>
          </div>
        </fieldset>
      </el-row>
      <!--  -->
      <el-row class="box" :gutter="40">
        <el-col :span="12">
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addLead" size="small" type="primary"> </el-button>
              <el-table
                border
                :data="leadsData"
                highlight-current-row
                style="width: 100%;"
                class="tableinfo"
                height="150px"
                :cell-class-name="cellClassName"
              >
                <el-table-column prop="deptname" label=""></el-table-column>
                <el-table-column prop="leadername" label=""></el-table-column>
                <el-table-column prop="agentname" label=""></el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    <el-button class="btn" @click="handleEdit2(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="handleDelete2(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
          <br />
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addDuty" size="small" type="primary"> </el-button>
              <el-table
                border
                :data="dutysData"
                highlight-current-row
                style="width: 100%;"
                class="tableinfo"
                height="150px"
                :cell-class-name="cellClassName"
              >
                <el-table-column prop="deptname" label=""></el-table-column>
                <el-table-column prop="leadername" label=""></el-table-column>
                <el-table-column prop="agentname" label=""></el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    <el-button class="btn" @click="handleEdit3(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="handleDelete3(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
        <el-col :span="12">
          <fieldset style="width:100%;">
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addRuleBtn" size="small" type="primary"> </el-button>
              <el-table
                border
                :data="ruleList"
                highlight-current-row
                style="width: 100%;"
                class="tableinfo"
                :cell-class-name="cellClassName"
                height="440px"
              >
                <el-table-column prop="stagename" label=""></el-table-column>
                <el-table-column prop="cycletype" label="">
                  <template slot-scope="scope">
                    <span v-html="ctypeFormat(scope,scope.row.cycletype)"></span>
                  </template>
                </el-table-column>
                <el-table-column prop="cycletime" label="">
                  <template slot-scope="scope">
                    <!--  -->
                    <p v-if="scope.row.cycletype==1">{{scope.row.cycletime}}</p>
                    <!--  -->
                    <!-- <p v-if="scope.row.cycletime==2"></p> -->
                    <!--  -->
                    <p v-else-if="scope.row.cycletype==3">{{scope.row.cycletime}}</p>
                    <!--  -->
                    <p v-else-if="scope.row.cycletype==4">{{scope.row.cycletime}}</p>
                    <!--  -->
                    <p v-else-if="scope.row.cycletype==5">{{scope.row.cycletime}}</p>
                  </template>
                </el-table-column>
                <el-table-column prop="warnday" label="()"></el-table-column>
                <el-table-column prop="starttime" label=""></el-table-column>
                <el-table-column prop="endtime" label=""></el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    <el-button class="btn" @click="editRuleBtn(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="delRuleBtn(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
      </el-row>
    </div>

    <div class="goback">
      <el-button @click="goback" size="small" type="warning"> </el-button>
      <el-button @click="projectSubmitAll" type="primary" size="small"> </el-button>
    </div>

    <!--  -->
    <el-dialog :title="ruleTitle" :visible.sync="ruleVisible" width="700px" class="rule-dialog">
      <el-form
        :model="addRuleForm"
        :inline="true"
        label-width="93px"
        ref="addRuleForm"
        :append-to-body="true"
        size="small"
      >
        <el-form-item label="" prop="stagename">
          <el-input v-model="addRuleForm.stagename" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" class="day" prop="warnday">
          <el-input v-model="addRuleForm.warnday">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="cycletype">
          <el-select
            v-model="addRuleForm.cycletype"
            clearable
            placeholder=""
            @change="cycleChange()"
          >
            <el-option
              v-for="item in submitType"
              :key="item.value"
              :label="item.type"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>

        <!-- 1  -->
        <el-form-item label="" class="day" prop="region" v-if="dateShow1">
          <el-input v-model="region">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <!-- 2  -->
        <el-form-item label="" prop="weekday" v-if="dateShow2">
          <el-select v-model="weekday" clearable placeholder="">
            <el-option
              v-for="item in weekList"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 3  -->
        <el-form-item label="" prop="monthday" v-if="dateShow3">
          <el-select v-model="monthday" clearable placeholder="">
            <el-option
              v-for="item in timeday"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 4  -->
        <el-form-item label="" prop="quarter" v-if="dateShow4">
          <el-select v-model="quarter" clearable placeholder="">
            <el-option
              v-for="item in timeday"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 5  -->
        <el-form-item label="" prop="daytime" v-if="dateShow5">
          <el-date-picker
            v-model="daytime"
            format="yyyy-MM-dd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>

        <el-form-item
          label=""
          prop="starttime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="addRuleForm.starttime"
            format="yyyy-MM-dd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label=""
          prop="endtime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="addRuleForm.endtime"
            format="yyyy-MM-dd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitRule" type="primary">{{ruleBtn}}</el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="taskTitle"
      :visible.sync="addLeadVisible"
      width="800px"
      class="task-dialog task-dialog2"
    >
      <el-form
        :inline="true"
        :model="addLeadData"
        label-width="100px"
        ref="addLeadData"
        :append-to-body="true"
      >
        <div class="leabox">
          <el-form-item
            label=""
            prop="deptid"
            :rules="[{ required: true, message: '', trigger: 'change' }]"
          >
            <el-select
              v-model="addLeadData.deptid"
              clearable
              filterable
              placeholder=""
              @change="deptChange"
            >
              <el-option
                v-for="item in leadunits"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="" prop="leadername">
          <el-input v-model="addLeadData.leadername"></el-input>
        </el-form-item>
        <el-form-item label="" prop="leaderphone">
          <el-input v-model="addLeadData.leaderphone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentname">
          <el-input v-model="addLeadData.agentname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentphone">
          <el-input v-model="addLeadData.agentphone"></el-input>
        </el-form-item>
        <div class="leabox leabox2">
          <el-form-item label="" prop="duty">
            <el-input type="textarea" :autosize="{ minRows: 10}" v-model="addLeadData.duty"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitLead" type="primary">{{changeWord}}</el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="taskTitle"
      :visible.sync="addDutyVisible"
      width="800px"
      class="task-dialog task-dialog2"
    >
      <el-form
        :inline="true"
        :model="addDutyData"
        label-width="100px"
        ref="addDutyData"
        :append-to-body="true"
      >
        <div class="leabox">
          <el-form-item
            label=""
            prop="deptid"
            :rules="[{ required: true, message: '', trigger: 'change' }]"
          >
            <el-select
              v-model="addDutyData.deptid"
              @change="deptChange2"
              clearable
              filterable
              :readonly="readOn"
              placeholder=""
            >
              <el-option
                v-for="item in leadunits"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="" prop="leadername">
          <el-input v-model="addDutyData.leadername"></el-input>
        </el-form-item>
        <el-form-item label="" prop="leaderphone">
          <el-input v-model="addDutyData.leaderphone" :readonly="readOn"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentname">
          <el-input v-model="addDutyData.agentname" :readonly="readOn"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentphone">
          <el-input v-model="addDutyData.agentphone" :readonly="readOn"></el-input>
        </el-form-item>
        <div class="leabox leabox2">
          <el-form-item label="" prop="duty">
            <el-input
              type="textarea"
              :autosize="{ minRows: 10}"
              :readonly="readOn"
              v-model="addDutyData.duty"
            ></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitDuty" type="primary">{{changeWord}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  projectTypeList,
  addProjectType,
  addProject,
  changeFormstate,
  synchFormstate,
  getAllProject,
  checkReportRules
} from "../../api/projectApi";
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  props: [
    "leadunits",
    "users",
    "leaderUsers",
    "streetdatas",
    "types",
    "theight"
  ],
  components: {},
  data() {
    return {
      isActive: true,
      error: null,
      checkAll: false,
      checkedItems: [],
      allitems: [],
      isIndeterminate: false,
      choosenum: 0,
      hasitem: [],
      // 
      submitType: [
        { value: 1, type: "" },
        // { value: 2, type: "" },
        { value: 3, type: "" },
        { value: 4, type: "" },
        { value: 5, type: "" }
      ],
      // 
      timeday: [
        { value: "1", name: "1" },
        { value: "2", name: "2" },
        { value: "3", name: "3" },
        { value: "4", name: "4" },
        { value: "5", name: "5" },
        { value: "6", name: "6" },
        { value: "7", name: "7" },
        { value: "8", name: "8" },
        { value: "9", name: "9" },
        { value: "10", name: "10" },
        { value: "11", name: "11" },
        { value: "12", name: "12" },
        { value: "13", name: "13" },
        { value: "14", name: "14" },
        { value: "15", name: "15" },
        { value: "16", name: "16" },
        { value: "17", name: "17" },
        { value: "18", name: "18" },
        { value: "19", name: "19" },
        { value: "20", name: "20" },
        { value: "21", name: "21" },
        { value: "22", name: "22" },
        { value: "23", name: "23" },
        { value: "24", name: "24" },
        { value: "25", name: "25" },
        { value: "26", name: "26" },
        { value: "27", name: "27" },
        { value: "28", name: "28" },
        { value: "29", name: "29" },
        { value: "30", name: "30" },
        { value: "31", name: "31" }
      ],
      newtask: "",
      taskShow: false,
      basicInfo: { category: "0", status: 0 },
      tasksData: [],
      leadsData: [],
      dutysData: [],
      addTaskData: { status: 0 },
      addLeadData: {},
      addDutyData: {},
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        arealeadids: [
          { required: true, message: "", trigger: "blur" }
        ],
        typeid: [{ required: true, message: "", trigger: "blur" }]
      },
      addTaskVisible: false,
      addLeadVisible: false,
      addDutyVisible: false,
      changeNum: "",
      changeWord: "",
      tindex: "-1",
      readOn: false,
      taskTitle: "",
      ruleList: [], //
      ruleVisible: false,
      ruleTitle: "",
      ruleBtn: "",
      addRuleForm: { cycletype: 1 }, //
      weekList: [
        { value: "1", name: "" },
        { value: "2", name: "" },
        { value: "3", name: "" },
        { value: "4", name: "" },
        { value: "5", name: "" },
        { value: "6", name: "" },
        { value: "7", name: "" }
      ],
      dateShow1: true, //
      dateShow2: false, //
      dateShow3: false, //
      dateShow4: false, //
      dateShow5: false, //
      region: "", //
      monthday: "", //
      quarter: "", //
      weekday: "", //
      daytime: "", //
      cycleNum: 1,
      ruleIndex: -1
    };
  },
  created() {},
  methods: {
    selectChange() {
      this.$forceUpdate();
    },
    // 
    ctypeFormat(scope, value) {
      switch (value) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
      }
    },
    // 
    cycleChange() {
      this.cycleNum = this.addRuleForm.cycletype;
      switch (this.cycleNum) {
        case 1:
          this.dateShow1 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 2:
          this.dateShow2 = true;
          this.dateShow1 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 3:
          this.dateShow3 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 4:
          this.dateShow4 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow5 = false;
          break;
        case 5:
          this.dateShow5 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow1 = false;
          break;
      }
    },
    // 
    addRuleBtn() {
      this.ruleVisible = true;
      this.ruleTitle = "";
      this.ruleBtn = "";
      this.changeNum = "add";
      this.classObject();
      this.addRuleForm = { cycletype: 1 };
      this.cycleChange();
    },
    // 
    addRuleSubmit() {
      this.$refs.addRuleForm.validate(valid => {
        if (valid) {
          this.cycleNum = this.addRuleForm.cycletype;
          if (this.cycleNum == 1) {
            this.addRuleForm.cycletime = this.region; //
          } else if (this.cycleNum == 2) {
            this.addRuleForm.cycletime = this.weekday; //
          } else if (this.cycleNum == 3) {
            this.addRuleForm.cycletime = this.monthday; //
          } else if (this.cycleNum == 4) {
            this.addRuleForm.cycletime = this.quarter; //
          } else if (this.cycleNum == 5) {
            this.addRuleForm.cycletime = this.daytime; //
          }
          let para = Object.assign({}, this.addRuleForm);
          this.ruleList.push(para);
          this.ruleVisible = false;
          this.ruleTitle = "";
          this.ruleBtn = "";
          this.changeNum = "";
          this.cycleNum = 0;
          this.region = ""; //
          this.monthday = ""; //
          this.quarter = ""; //
          this.weekday = ""; //
          this.daytime = ""; //
        } else {
          return false;
        }
      });
    },
    // 
    editRuleBtn(index, row) {
      this.hasitem = [];
      if (row.pidList) {
        this.checkedItems = row.pidList;
      } else {
        this.checkedItems = [];
      }
      var ctype = row.cycletype;
      this.cycleNum = row.cycletype;
      this.hasitem = [];
      switch (ctype) {
        case 1:
          this.dateShow1 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.region = row.cycletime;
          break;
        case 2:
          this.dateShow2 = true;
          this.dateShow1 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.weekday = row.cycletime;
          break;
        case 3:
          this.dateShow3 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.monthday = row.cycletime;
          break;
        case 4:
          this.dateShow4 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow5 = false;
          this.quarter = row.cycletime;
          break;
        case 5:
          this.dateShow5 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow1 = false;
          this.daytime = row.cycletime;
          break;
      }

      this.ruleVisible = true;
      this.ruleTitle = "";
      this.ruleBtn = "";
      this.changeNum = "edit";
      this.addRuleForm = Object.assign({}, row);
      this.ruleIndex = index;
    },
    // 
    editRuleSubmit() {
      this.$refs.addRuleForm.validate(valid => {
        if (valid) {
          // 
          let spara = {
            pidList: this.checkedItems,
            starttime: this.addRuleForm.starttime,
            endtime: this.addRuleForm.endtime
          };
          spara = JSON.stringify(spara);
          checkReportRules(spara, data => {
            this.hasitem = data.data.obj;
            if (this.hasitem.length > 0) {
              this.$confirm(
                "<span style='color:#f00;'></span>",
                "",
                {
                  dangerouslyUseHTMLString: true,
                  type: "warning",
                  center: true,
                  modal: false,
                  confirmButtonText: ""
                }
              ).then(() => {
                var array1 = this.checkedItems;
                var array2 = this.hasitem;
                var tempArray1 = [];
                var tempArray2 = [];
                for (var m = 0; m < array2.length; m++) {
                  tempArray1[array2[m]] = true; //array2 tempArray1 true
                }
                for (var i = 0; i < array1.length; i++) {
                  if (!tempArray1[array1[i]]) {
                    tempArray2.push(array1[i]); //array1 array2 
                  }
                }
                this.checkedItems = tempArray2; //
              });
            } else {
              let para = Object.assign({}, this.addRuleForm);
              para.pidList = this.checkedItems;
              this.cycleNum = this.addRuleForm.cycletype;
              if (this.cycleNum == 1) {
                para.cycletime = this.region; //
              } else if (this.cycleNum == 2) {
                para.cycletime = this.weekday; //
              } else if (this.cycleNum == 3) {
                para.cycletime = this.monthday; //
              } else if (this.cycleNum == 4) {
                para.cycletime = this.quarter; //
              } else if (this.cycleNum == 5) {
                para.cycletime = this.daytime; //
              }
              this.ruleList.splice(this.ruleIndex, 1, para);
              this.$refs["addRuleForm"].resetFields();
              this.ruleVisible = false;
              this.ruleIndex = -1;
              this.ruleTitle = "";
              this.ruleBtn = "";
              this.changeNum = "";
              this.cycleNum = 0;
              this.region = ""; //
              this.monthday = ""; //
              this.quarter = ""; //
              this.weekday = ""; //
              this.daytime = ""; //
            }
          });
        } else {
          return false;
        }
      });
    },
    // 
    delRuleBtn(index, row) {
      this.ruleList.splice(index, 1);
      this.ruleVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },
    // 
    submitRule() {
      if (this.changeNum == "add") {
        this.addRuleSubmit();
      } else {
        this.editRuleSubmit();
      }
    },
    // 
    getTypes: function() {
      projectTypeList("", data => {
        this.types = data.data.obj;
      });
    },
    // 
    newTask() {
      let para = { name: this.newtask };
      addProjectType(para, res => {
        this.$message({
          message: "",
          type: "success"
        });
        this.getTypes();
        this.newtask = "";
      });
    },
    goback() {
      this.$confirm(
        "<span style='color:#f00;'></span>",
        "",
        {
          dangerouslyUseHTMLString: true,
          type: "warning",
          center: true,
          modal: true,
          confirmButtonText: "",
          cancelButtonText: ""
        }
      )
        .then(() => {
          this.$emit("my-event");
        })
        .catch(() => {
          return false;
        });
    },
    // 
    statusFormat(scope, value) {
      switch (value) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case -1:
          return "";
          break;
      }
    },
    // 
    cellClassName({ row, column, rowIndex }) {
      let status = row.status;
      switch (status) {
        case 1:
          return "yellow";
          break;
        case 2:
          return "red";
          break;
        case 3:
          return "zh";
          break;
        case 4:
          return "zhou";
          break;
        case -1:
          return "gray";
          break;
          return "";
      }
    },
    // -----------------------------------------
    // 
    addLead: function() {
      this.changeNum = "add";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addLeadVisible = true;
    },
    // 
    handleDetail2(index, row) {
      this.changeNum = "read";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = true;
      this.addLeadVisible = true;
      this.addLeadData = Object.assign({}, row);
    },
    // 
    handleEdit2(index, row) {
      this.changeNum = "edit";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addLeadVisible = true;
      this.addLeadData = Object.assign({}, row);
      this.tindex = index;
    },
    // -
    submitLead: function() {
      if (this.changeNum == "read") {
        this.addLeadVisible = false;
      } else if (this.changeNum == "add") {
        this.addLeadSubmit();
      } else {
        this.editLeadSubmit();
      }
    },
    deptChange() {
      let deptname = "";
      for (var i = 0; i < this.leadunits.length; i++) {
        if (this.leadunits[i].id == this.addLeadData.deptid) {
          deptname = this.leadunits[i].name;
          break;
        }
      }
      this.addLeadData.deptname = deptname;
    },
    // 
    addLeadSubmit: function() {
      this.$refs.addLeadData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addLeadData);
          this.leadsData.push(para);
          this.$refs["addLeadData"].resetFields();
          this.addLeadVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    editLeadSubmit: function() {
      this.$refs.addLeadData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addLeadData);
          this.leadsData.splice(this.tindex, 1, para);
          this.$refs["addLeadData"].resetFields();
          this.addLeadVisible = false;
        } else {
          console.log("error");
          return false;
        }
      });
    },
    // 
    handleDelete2(index, row) {
      this.leadsData.splice(index, 1);
      this.$refs["addLeadData"].resetFields();
      this.addLeadVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },

    // -----------------------------------------
    // 
    addDuty: function() {
      this.changeNum = "add";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addDutyVisible = true;
    },
    // 
    handleDetail3(index, row) {
      this.changeNum = "read";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = true;
      this.addDutyVisible = true;
      this.addDutyData = Object.assign({}, row);
    },
    // 
    handleEdit3(index, row) {
      this.changeNum = "edit";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addDutyVisible = true;
      this.addDutyData = Object.assign({}, row);
      this.tindex = index;
    },
    // -
    submitDuty: function() {
      if (this.changeNum == "read") {
        this.addDutyVisible = false;
      } else if (this.changeNum == "add") {
        this.addDutySubmit();
      } else {
        this.editDutySubmit();
      }
    },
    deptChange2() {
      let deptname = "";
      for (var i = 0; i < this.leadunits.length; i++) {
        if (this.leadunits[i].id == this.addDutyData.deptid) {
          deptname = this.leadunits[i].name;
          break;
        }
      }
      this.addDutyData.deptname = deptname;
    },
    // 
    addDutySubmit: function() {
      this.$refs.addDutyData.validate(valid => {
        if (valid) {
          console.log(this.addDutyData);
          let para = Object.assign({}, this.addDutyData);
          this.dutysData.push(para);
          this.$refs["addDutyData"].resetFields();
          this.addDutyVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    editDutySubmit: function() {
      this.$refs.addDutyData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addDutyData);
          this.dutysData.splice(this.tindex, 1, para);
          this.$refs["addDutyData"].resetFields();
          this.addDutyVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    handleDelete3(index, row) {
      this.dutysData.splice(index, 1);
      this.$refs["addDutyData"].resetFields();
      this.addDutyVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },

    //  --------------
    projectSubmitAll: function() {
      this.$refs.basicInfo.validate(valid => {
        if (valid) {
          if (this.leadsData == []) {
            this.$message({
              message: "",
              type: "warning"
            });
          } else {
            var str = "";
            for (var i = 0; i < this.basicInfo.street.length; i++) {
              str += this.basicInfo.street[i] + ",";
            }
            this.basicInfo.streetids = str;
            if (this.basicInfo.street.length > 1) {
              str = str.substr(0, str.length - 1);
            }
            let para = this.basicInfo;
            para.arealeadids = this.basicInfo.arealeadids + ",";
            let { street, ...others } = para;
            para = others;
            para.leaderList = this.leadsData;
            para.dutyList = this.dutysData;
            para.ruleList = this.ruleList;
            para.totalstatus = 0;
            para.formstatus = 0;
            para = JSON.stringify(para);
            addProject(para, res => {
              if (res.data.code == -2) {
                this.$message({
                  message: res.data.msg,
                  type: "error"
                });
              } else {
                if (res.data.code == 0) {
                  let cpara = { pid: res.data.obj };
                  changeFormstate(cpara, data => {
                    synchFormstate({ pid: res.data.obj }, ress => {
                      this.$emit("my-event");
                      this.$emit("getAll-event");
                      this.$message({
                        message: "",
                        type: "success"
                      });
                    });
                  });
                } else {
                  this.$message({
                    message: "",
                    type: "error"
                  });
                  this.$emit("my-event");
                  this.$emit("getAll-event");
                }
              }
            });
          }
        } else {
          this.$message({
            message: "",
            type: "warning"
          });
          return false;
        }
      });
    }
  }
};
</script>

<style lang="scss">
.addproject {
  padding-top: 20px;
  .sxtitle {
    // overflow: hidden;
    // text-overflow: ellipsis;
    // white-space: nowrap;
    // cursor: pointer;
    text-align: left;
  }
  .back {
    margin-bottom: 20px;
  }
  .jiabtn {
    height: 36px;
    line-height: 36px;
    position: relative;
    .icon {
      color: #666;
      font-size: 24px;
      display: inline-block;
      height: 36px;
      line-height: 36px;
      padding: 0 10px;
      cursor: pointer;
    }
    .awrap {
      position: absolute;
      left: 44px;
      top: 0;
      border: 1px solid #ccc;
      background: #fff;
      width: 250px;
      padding: 5px 10px 10px;
      z-index: 99;
      p {
        color: #666;
      }
      .el-textarea {
        width: 100%;
        margin-bottom: 10px;
      }
      .buttons {
        display: flex;
        justify-content: center;
        .el-button {
          padding: 8px 18px;
        }
      }
    }
  }
  .box {
    margin-bottom: 30px;
    .boxcontent {
      padding: 20px;
      .addbtn {
        margin-bottom: 20px;
      }
    }
    .basicbox {
      .blist {
        display: flex;
        .el-form-item {
          flex: 1;
          display: flex;
          .el-form-item__content {
            flex: 1;
            .el-select {
              width: 100%;
            }
          }
        }
      }
      .jwlist {
        .el-form-item {
          flex: none;
          width: 300px;
        }
      }
      .bsrequire {
        display: flex;
        .el-form-item__content {
          flex: 1;
        }
      }
    }
  }
  .task-dialog {
    .taskbox {
      display: flex;
      .day {
        .el-input__inner {
          width: 80px;
          border-radius: 4px;
        }
        .el-input-group__append {
          padding: 0 8px;
          background: #fff;
          border: 0;
          color: #222;
        }
      }
      .yq {
        .el-form-item__content {
          width: 120px;
        }
      }
      .bj {
        .el-select {
          width: 100px;
        }
      }
    }
    .leabox {
      .el-form-item {
        width: 100%;
      }
    }
  }
  .task-dialog2 {
    .el-form--inline .el-form-item__content {
      width: 260px;
      .el-select {
        width: 100%;
      }
    }
    .leabox2 {
      .el-form-item__content {
        width: 634px;
      }
    }
  }
  .goback {
    padding: 30px 0 10px;
    display: flex;
    justify-content: flex-end;
    .el-button {
      padding: 10px 30px;
    }
    .el-button + .el-button {
      margin-left: 20px;
    }
  }
}
</style>



