<template>
  <div class="addproject">
    <div class="back">
      <el-button @click="goback" size="small" type="primary"></el-button>
    </div>
    <div class="addcontent" :style="theight">
      <el-row class="box" :gutter="20" style="display:flex;align-items:stretch;">
        <el-col :span="12">
          <fieldset style="height:100%;">
            <legend></legend>
            <div class="boxcontent basicbox detail-basic2">
              <el-form
                label-width="120px"
                size="medium"
                ref="basicInfo"
                :rules="rules"
                :model="basicInfo"
              >
                <el-form-item label="" prop="name">
                  <el-input placeholder="" v-model="basicInfo.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="owndeptid">
                  <el-select
                    v-model="basicInfo.owndeptid"
                    filterable
                    clearable
                    placeholder=""
                    @change="selectChange()"
                  >
                    <el-option
                      v-for="item in leadunits"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="arealeadids" class="ww1">
                  <el-select
                    v-model="basicInfo.arealeadids"
                    filterable
                    collapse-tags
                    multiple
                    clearable
                    placeholder=""
                    @change="selectChange"
                  >
                    <el-option
                      v-for="item in leaderUsers"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="streetids" class="ww1">
                  <el-select
                    v-model="basicInfo.streetids"
                    filterable
                    multiple
                    collapse-tags
                    clearable
                    placeholder=""
                    @change="selectChange"
                  >
                    <el-option
                      v-for="item in streetdatas"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="category">
                  <el-radio @change="selectChange" v-model="basicInfo.category" label="0"></el-radio>
                  <el-radio @change="selectChange" v-model="basicInfo.category" label="1"></el-radio>
                </el-form-item>
                <el-form-item label="" prop="address">
                  <el-input placeholder="" v-model="basicInfo.address"></el-input>
                </el-form-item>
                <el-form-item label="" prop="mapitude">
                  <el-input placeholder="" v-model="basicInfo.mapitude"></el-input>
                </el-form-item>
                <el-form-item label="" prop="submitres">
                  <el-input type="textarea" :rows="2" v-model="basicInfo.submitres"></el-input>
                </el-form-item>
                <el-form-item label="" prop="remark">
                  <el-input type="textarea" :rows="2" v-model="basicInfo.remark"></el-input>
                </el-form-item>
                <el-form-item label="" prop="aimplan">
                  <el-input type="textarea" :rows="2" v-model="basicInfo.aimplan"></el-input>
                </el-form-item>
              </el-form>
            </div>
          </fieldset>
        </el-col>
        <!--  -->
        <el-col :span="12">
          <fieldset class="plist" style="height:100%;overflow:hidden;">
            <legend></legend>
            <div class="unitbox" style="padding:15px;">
              <div class="speed">
                <div class="pbox">
                  <p class="tt"></p>
                  <div class="conbox">
                    <p v-for="(item, index) in lastprogress" :key="index">{{item.question}}</p>
                  </div>
                </div>
                <div class="pbox">
                  <p class="tt"></p>
                  <div class="conbox">
                    <p v-for="(item, index) in newReportList" :key="index">{{item.question}}</p>
                  </div>
                </div>
                <div class="pbox">
                  <p class="tt"></p>
                  <div class="conbox">
                    <p v-for="(item, index) in newReportList" :key="index">{{item.wenti}}</p>
                  </div>
                </div>
                <div class="pbox">
                  <p class="tt"></p>
                  <div class="conbox">
                    <p v-for="(item, index) in newReportList" :key="index">{{item.measure}}</p>
                  </div>
                </div>
                <div class="filelist">
                  <p class="tt"></p>
                  <div class="list">
                    <div>
                      <div v-for="(item, index) in newReportList" :key="index">
                        <div v-for="(sitem, index) in item.newFiles" :key="index">
                          <a
                            :href="sitem.url"
                            target="_blank"
                            style="text-align:left;"
                            class="filetitle"
                          >{{index+1}}. {{sitem.name}}</a>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </fieldset>
        </el-col>
      </el-row>
      <!--  -->
      <el-row class="box" :gutter="40">
        <el-col :span="12">
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addLead" size="small" type="primary"> </el-button>
              <el-table
                border
                :data="leadsData"
                highlight-current-row
                style="width: 100%;"
                class="tableinfo"
                :cell-class-name="cellClassName"
                height="150px"
              >
                <el-table-column prop="deptName" label=""></el-table-column>
                <el-table-column prop="leadername" label=""></el-table-column>
                <el-table-column prop="agentname" label=""></el-table-column>
                <el-table-column prop="formstatus" label="">
                  <template slot-scope="scope">
                    <div>
                      <el-tag size="small">
                        <i
                          class="fa mark"
                          v-if="scope.row.formstatus==1"
                          style="background:#fbe804;"
                        ></i>
                        <i
                          class="fa mark"
                          v-else-if="scope.row.formstatus==2"
                          style="background:#f00;"
                        ></i>
                        <i
                          class="fa mark"
                          v-else-if="scope.row.formstatus== -1"
                          style="background:#999;"
                        ></i>
                        <i class="fa mark" v-else></i>
                        <span v-html="formstatusFormat(scope,scope.row.formstatus)"></span>
                      </el-tag>
                    </div>
                  </template>
                </el-table-column>
                <el-table-column label="" width="120">
                  <template scope="scope">
                    <el-button class="btn" @click="handleEdit2(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="handleDelete2(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
          <br />
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addDuty" size="small" type="primary"> </el-button>
              <el-table
                border
                :data="dutysData"
                highlight-current-row
                style="width: 100%;"
                class="tableinfo"
                :cell-class-name="cellClassName"
                height="150px"
              >
                <el-table-column prop="deptName" label=""></el-table-column>
                <el-table-column prop="leadername" label=""></el-table-column>
                <el-table-column prop="agentname" label=""></el-table-column>
                <el-table-column label="" width="120">
                  <template scope="scope">
                    <el-button class="btn" @click="handleEdit3(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="handleDelete3(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
        <el-col :span="12">
          <fieldset style="width:100%;">
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addRuleBtn" size="small" type="primary"> </el-button>
              <el-table
                border
                :data="ruleList"
                highlight-current-row
                style="width: 100%;"
                class="tableinfo"
                :cell-class-name="cellClassName"
                height="435px"
              >
                <el-table-column prop="stagename" label=""></el-table-column>
                <el-table-column prop="cycletype" label="">
                  <template slot-scope="scope">
                    <span v-html="ctypeFormat(scope,scope.row.cycletype)"></span>
                  </template>
                </el-table-column>
                <el-table-column prop="cycletime" label="">
                  <template slot-scope="scope">
                    <!--  -->
                    <p v-if="scope.row.cycletype==1">{{scope.row.cycletime}}</p>
                    <!--  -->
                    <p v-if="scope.row.cycletype==2">{{scope.row.cycletime}}</p>
                    <!--  -->
                    <p v-else-if="scope.row.cycletype==3">{{scope.row.cycletime}}</p>
                    <!--  -->
                    <p v-else-if="scope.row.cycletype==4">{{scope.row.cycletime}}</p>
                    <!--  -->
                    <p v-else-if="scope.row.cycletype==5">{{scope.row.cycletime}}</p>
                  </template>
                </el-table-column>
                <el-table-column prop="warnday" width="110px" label="()"></el-table-column>
                <el-table-column prop="starttime" :formatter="starttimeToTime" label=""></el-table-column>
                <el-table-column prop="endtime" :formatter="endtimeToTime" label=""></el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    <el-button class="btn" @click="editRuleBtn(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="delRuleBtn(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
      </el-row>
    </div>
    <div class="goback">
      <el-button @click="goback" type="warning" size="small"> </el-button>
      <el-button @click="projectSubmitAll" size="small" type="primary"> </el-button>
    </div>

    <!--  -->
    <el-dialog :title="ruleTitle" :visible.sync="ruleVisible" width="700px" class="rule-dialog">
      <el-form
        :model="addRuleForm"
        label-width="93px"
        :inline="true"
        ref="addRuleForm"
        :append-to-body="true"
        size="small"
      >
        <el-form-item label="" prop="stagename">
          <el-input v-model="addRuleForm.stagename" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" class="day" prop="warnday">
          <el-input v-model="addRuleForm.warnday">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="cycletype">
          <el-select
            v-model="addRuleForm.cycletype"
            clearable
            placeholder=""
            @change="cycleChange()"
          >
            <el-option
              v-for="item in submitType"
              :key="item.value"
              :label="item.type"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>

        <!-- 1  -->
        <el-form-item label="" class="day" prop="region" v-if="dateShow1">
          <el-input v-model="region">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <!-- 2  -->
        <el-form-item label="" prop="weekday" v-if="dateShow2">
          <el-select v-model="weekday" clearable placeholder="">
            <el-option
              v-for="item in weekList"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 3  -->
        <el-form-item label="" prop="monthday" v-if="dateShow3">
          <el-select v-model="monthday" clearable placeholder="">
            <el-option
              v-for="item in timeday"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 4  -->
        <el-form-item label="" prop="quarter" v-if="dateShow4">
          <el-select v-model="quarter" clearable placeholder="">
            <el-option
              v-for="item in timeday"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 5  -->
        <el-form-item label="" prop="daytime" v-if="dateShow5">
          <el-date-picker
            v-model="daytime"
            format="yyyy-MM-dd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>

        <el-form-item
          label=""
          prop="starttime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="addRuleForm.starttime"
            format="yyyy-MM-dd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label=""
          prop="endtime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="addRuleForm.endtime"
            format="yyyy-MM-dd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitRule" type="primary">{{ruleBtn}}</el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="taskTitle"
      :visible.sync="addLeadVisible"
      width="800px"
      class="task-dialog task-dialog2"
    >
      <el-form
        :inline="true"
        :model="addLeadData"
        label-width="100px"
        ref="addLeadData"
        :append-to-body="true"
      >
        <div class="leabox">
          <el-form-item
            label=""
            prop="deptid"
            :rules="[{ required: true, message: '', trigger: 'change' }]"
          >
            <el-select
              v-model="addLeadData.deptid"
              clearable
              filterable
              placeholder=""
              @change="deptChange"
            >
              <el-option
                v-for="item in leadunits"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="" prop="leadername">
          <el-input v-model="addLeadData.leadername"></el-input>
        </el-form-item>
        <el-form-item label="" prop="leaderphone">
          <el-input v-model="addLeadData.leaderphone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentname">
          <el-input v-model="addLeadData.agentname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentphone">
          <el-input v-model="addLeadData.agentphone"></el-input>
        </el-form-item>
        <div class="leabox leabox2">
          <el-form-item label="" prop="duty">
            <el-input type="textarea" :autosize="{ minRows: 10}" v-model="addLeadData.duty"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitLead" type="primary">{{changeWord}}</el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="taskTitle"
      :visible.sync="addDutyVisible"
      width="800px"
      class="task-dialog task-dialog2"
    >
      <el-form
        :inline="true"
        :model="addDutyData"
        label-width="100px"
        ref="addDutyData"
        :append-to-body="true"
      >
        <div class="leabox">
          <el-form-item
            label=""
            prop="deptid"
            :rules="[{ required: true, message: '', trigger: 'change' }]"
          >
            <el-select
              v-model="addDutyData.deptid"
              @change="deptChange2"
              clearable
              filterable
              :readonly="readOn"
              placeholder=""
            >
              <el-option
                v-for="item in leadunits"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="" prop="leadername">
          <el-input v-model="addDutyData.leadername"></el-input>
        </el-form-item>
        <el-form-item label="" prop="leaderphone">
          <el-input v-model="addDutyData.leaderphone" :readonly="readOn"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentname">
          <el-input v-model="addDutyData.agentname" :readonly="readOn"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentphone">
          <el-input v-model="addDutyData.agentphone" :readonly="readOn"></el-input>
        </el-form-item>
        <div class="leabox leabox2">
          <el-form-item label="" prop="duty">
            <el-input
              type="textarea"
              :autosize="{ minRows: 10}"
              :readonly="readOn"
              v-model="addDutyData.duty"
            ></el-input>
          </el-form-item>
        </div>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="submitDuty" type="primary">{{changeWord}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  projectTypeList,
  addProjectType,
  editProject,
  targetOver,
  targetbyId,
  targetLag,
  changeFormstate,
  synchFormstate,
  getAllProject,
  checkReportRules
} from "../../api/projectApi";
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  props: [
    "leadunits",
    "users",
    "projectId",
    "basicInfo",
    "leadsData",
    "dutysData",
    "leaderUsers",
    "types",
    "streetdatas",
    "theight",
    "ruleList",
    "newReportList",
    "lastprogress"
  ],
  components: {},
  data() {
    return {
      checkAll: false,
      checkedItems: [],
      allitems: [],
      isIndeterminate: false,
      choosenum: 0,
      hasitem: [],
      // end
      newtask: "",
      taskShow: false,
      addTaskData: { status: 0 },
      addLeadData: {},
      addDutyData: {},
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        arealeadids: [
          { required: true, message: "", trigger: "blur" }
        ],
        typeid: [{ required: true, message: "", trigger: "blur" }]
      },
      addTaskVisible: false,
      addLeadVisible: false,
      addDutyVisible: false,
      changeNum: "",
      changeWord: "",
      tindex: "-1",
      readOn: false,
      taskTitle: "",
      ruleVisible: false,
      dateShow1: false, //
      dateShow2: false, //
      dateShow3: false, //
      dateShow4: false, //
      dateShow5: false, //
      region: "", //
      monthday: "", //
      quarter: "", //
      weekday: "", //
      daytime: "", //
      ruleTitle: "",
      ruleBtn: "",
      cycleNum: 0,
      addRuleForm: { cycletype: 1 }, //
      weekList: [
        { value: "1", name: "" },
        { value: "2", name: "" },
        { value: "3", name: "" },
        { value: "4", name: "" },
        { value: "5", name: "" },
        { value: "6", name: "" },
        { value: "7", name: "" }
      ],
      // 
      submitType: [
        { value: 1, type: "" },
        // { value: 2, type: "" },
        { value: 3, type: "" },
        { value: 4, type: "" },
        { value: 5, type: "" }
      ],
      // 
      timeday: [
        { value: "1", name: "1" },
        { value: "2", name: "2" },
        { value: "3", name: "3" },
        { value: "4", name: "4" },
        { value: "5", name: "5" },
        { value: "6", name: "6" },
        { value: "7", name: "7" },
        { value: "8", name: "8" },
        { value: "9", name: "9" },
        { value: "10", name: "10" },
        { value: "11", name: "11" },
        { value: "12", name: "12" },
        { value: "13", name: "13" },
        { value: "14", name: "14" },
        { value: "15", name: "15" },
        { value: "16", name: "16" },
        { value: "17", name: "17" },
        { value: "18", name: "18" },
        { value: "19", name: "19" },
        { value: "20", name: "20" },
        { value: "21", name: "21" },
        { value: "22", name: "22" },
        { value: "23", name: "23" },
        { value: "24", name: "24" },
        { value: "25", name: "25" },
        { value: "26", name: "26" },
        { value: "27", name: "27" },
        { value: "28", name: "28" },
        { value: "29", name: "29" },
        { value: "30", name: "30" },
        { value: "31", name: "31" }
      ],
      ruleIndex: -1,
    };
  },
  created() {
  },
  methods: {
    // 
    formstatusFormat(scope, value) {
      switch (value) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case -1:
          return "";
          break;
      }
    },
    endtimeToTime(row, column) {
      var date = new Date(row.endtime);
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate();
      return Y + M + D;
    },
    starttimeToTime(row, column) {
      var date = new Date(row.starttime);
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate();
      return Y + M + D;
    },
    // 
    ctypeFormat(scope, value) {
      switch (value) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
      }
    },
    // 
    cycleChange() {
      this.cycleNum = this.addRuleForm.cycletype;
      switch (this.cycleNum) {
        case 1:
          this.dateShow1 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 2:
          this.dateShow2 = true;
          this.dateShow1 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 3:
          this.dateShow3 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 4:
          this.dateShow4 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow5 = false;
          break;
        case 5:
          this.dateShow5 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow1 = false;
          break;
      }
    },
    // 
    addRuleBtn() {
      this.ruleVisible = true;
      this.ruleTitle = "";
      this.ruleBtn = "";
      this.changeNum = "add";
      this.addRuleForm = { cycletype: 1 };
      this.cycleChange();
    },
    // 
    addRuleSubmit() {
      this.$refs.addRuleForm.validate(valid => {
        if (valid) {
          this.cycleNum = this.addRuleForm.cycletype;
          if (this.cycleNum == 1) {
            this.addRuleForm.cycletime = this.region; //
          } else if (this.cycleNum == 2) {
            this.addRuleForm.cycletime = this.weekday; //
          } else if (this.cycleNum == 3) {
            this.addRuleForm.cycletime = this.monthday; //
          } else if (this.cycleNum == 4) {
            this.addRuleForm.cycletime = this.quarter; //
          } else if (this.cycleNum == 5) {
            this.addRuleForm.cycletime = this.daytime; //
          }
          let para = Object.assign({}, this.addRuleForm);
          this.ruleList.push(para);
          this.$refs["addRuleForm"].resetFields();
          this.ruleVisible = false;
          this.ruleTitle = "";
          this.ruleBtn = "";
          this.changeNum = "";
          this.cycleNum = 0;
        } else {
          return false;
        }
      });
    },
    // 
    editRuleBtn(index, row) {
      if (row.pidList) {
        this.checkedItems = row.pidList;
      } else {
        this.checkedItems = [];
      }
      this.hasitem = [];
      var ctype = row.cycletype;
      this.cycleNum = row.cycletype;
      switch (ctype) {
        case 1:
          this.dateShow1 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.region = row.cycletime;
          break;
        case 2:
          this.dateShow2 = true;
          this.dateShow1 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.weekday = row.cycletime;
          break;
        case 3:
          this.dateShow3 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.monthday = row.cycletime;
          break;
        case 4:
          this.dateShow4 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow5 = false;
          this.quarter = row.cycletime;
          break;
        case 5:
          this.dateShow5 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow1 = false;
          this.daytime = row.cycletime;
          break;
      }
      this.ruleVisible = true;
      this.ruleTitle = "";
      this.ruleBtn = "";
      this.changeNum = "edit";
      this.addRuleForm = Object.assign({}, row);
      this.addRuleForm.endtime = new Date(this.addRuleForm.endtime);
      this.addRuleForm.starttime = new Date(this.addRuleForm.starttime);
      this.addRuleForm.endtime = new Date(this.addRuleForm.endtime);
      this.ruleIndex = index;
    },
    // 
    editRuleSubmit() {
      this.$refs.addRuleForm.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addRuleForm);
          this.cycleNum = this.addRuleForm.cycletype;
          if (this.cycleNum == 1) {
            para.cycletime = this.region; //
          } else if (this.cycleNum == 2) {
            para.cycletime = this.weekday; //
          } else if (this.cycleNum == 3) {
            para.cycletime = this.monthday; //
          } else if (this.cycleNum == 4) {
            para.cycletime = this.quarter; //
          } else if (this.cycleNum == 5) {
            para.cycletime = this.daytime; //
          }
          this.ruleList.splice(this.ruleIndex, 1, para);
          this.$refs["addRuleForm"].resetFields();
          this.ruleVisible = false;
          this.ruleIndex = -1;
          this.ruleTitle = "";
          this.ruleBtn = "";
          this.changeNum = "";
          this.cycleNum = 0;
        } else {
          return false;
        }
      });
    },
    // 
    delRuleBtn(index, row) {
      this.ruleList.splice(index, 1);
      this.ruleVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },
    // 
    submitRule() {
      if (this.changeNum == "add") {
        this.addRuleSubmit();
      } else {
        this.editRuleSubmit();
      }
    },
    selectChange() {
      this.$forceUpdate();
    },
    timestampToTime(row, column) {
      var date = new Date(row.stoptime);
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      return Y + M + D;
    },
    // 
    getTypes: function() {
      projectTypeList("", data => {
        this.types = data.data.obj;
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (row.status == -1) {
        return "noedit";
      } else if (row.status == 3) {
        return "lagbtn";
      }
    },
    // 
    newTask() {
      let para = { name: this.newtask };
      addProjectType(para, res => {
        this.$message({
          message: "",
          type: "success"
        });
        this.getTypes();
        this.newtask = "";
      });
    },
    goback() {
      this.$confirm(
        "<span style='color:#f00;'></span>",
        "",
        {
          dangerouslyUseHTMLString: true,
          type: "warning",
          center: true,
          modal: true,
          confirmButtonText: "",
          cancelButtonText: ""
        }
      )
        .then(() => {
          this.$emit("my-event");
        })
        .catch(() => {
          return false;
        });
    },
    // 
    statusFormat(scope, value) {
      switch (value) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case -1:
          return "";
          break;
      }
    },
    // 
    cellClassName({ row, column, rowIndex }) {
      let status = row.status;
      switch (status) {
        case 1:
          return "yellow";
          break;
        case 2:
          return "red";
          break;
        case 3:
          return "zh";
          break;
        case 4:
          return "zhou";
          break;
        case -1:
          return "gray";
          break;
          return "";
      }
    },
    testFuc() {
      console.log(this.basicInfo.streetids);
    },

    // -----------------------------------------
    // 
    addLead: function() {
      this.changeNum = "add";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addLeadVisible = true;
    },
    // 
    handleDetail2(index, row) {
      this.changeNum = "read";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = true;
      this.addLeadVisible = true;
      this.addLeadData = Object.assign({}, row);
    },
    // 
    handleEdit2(index, row) {
      this.changeNum = "edit";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addLeadVisible = true;
      this.addLeadData = Object.assign({}, row);
      this.tindex = index;
    },
    // -
    submitLead: function() {
      if (this.changeNum == "read") {
        this.addLeadVisible = false;
      } else if (this.changeNum == "add") {
        this.addLeadSubmit();
      } else {
        this.editLeadSubmit();
      }
    },
    deptChange() {
      let deptname = "";
      let leadid = "";
      let deptphone = "";
      let leadname = "";
      for (var i = 0; i < this.leadunits.length; i++) {
        if (this.leadunits[i].id == this.addLeadData.deptid) {
          deptname = this.leadunits[i].name;
          leadid = this.leadunits[i].deptLeader;
          deptphone = this.leadunits[i].deptphone;
          break;
        }
      }
      for (var j = 0; j < this.users.length; j++) {
        if (this.users[j].id == leadid) {
          leadname = this.users[j].name;
          break;
        }
      }
      this.addLeadData.deptName = deptname;
      this.addLeadData.leaderid = leadid;
      this.addLeadData.leadername = leadname;
      this.addLeadData.leaderphone = deptphone;
    },
    // 
    addLeadSubmit: function() {
      this.$refs.addLeadData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addLeadData);
          this.leadsData.push(para);
          this.$refs["addLeadData"].resetFields();
          this.addLeadVisible = false;
        } else {
          console.log("error");
          return false;
        }
      });
    },
    // 
    editLeadSubmit: function() {
      this.$refs.addLeadData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addLeadData);
          this.leadsData.splice(this.tindex, 1, para);
          this.$refs["addLeadData"].resetFields();
          this.addLeadVisible = false;
          console.log(this.leadsData)
        } else {
          return false;
        }
      });
    },

    // 
    handleDelete2(index, row) {
      this.leadsData.splice(index, 1);
      this.$refs["addLeadData"].resetFields();
      this.addLeadVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },

    // -----------------------------------------
    // 
    addDuty: function() {
      this.changeNum = "add";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addDutyVisible = true;
    },
    // 
    handleDetail3(index, row) {
      this.changeNum = "read";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = true;
      this.addDutyVisible = true;
      this.addDutyData = Object.assign({}, row);
    },
    // 
    handleEdit3(index, row) {
      this.changeNum = "edit";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addDutyVisible = true;
      this.addDutyData = Object.assign({}, row);
      this.tindex = index;
    },
    // -
    submitDuty: function() {
      if (this.changeNum == "read") {
        this.addDutyVisible = false;
      } else if (this.changeNum == "add") {
        this.addDutySubmit();
      } else {
        this.editDutySubmit();
      }
    },
    deptChange2() {
      let deptname = "";
      let leadid = "";
      let deptphone = "";
      let leadname = "";
      for (var i = 0; i < this.leadunits.length; i++) {
        if (this.leadunits[i].id == this.addDutyData.deptid) {
          deptname = this.leadunits[i].name;
          leadid = this.leadunits[i].deptLeader;
          deptphone = this.leadunits[i].deptphone;
          break;
        }
      }
      for (var j = 0; j < this.users.length; j++) {
        if (this.users[j].id == leadid) {
          leadname = this.users[j].name;
          break;
        }
      }
      this.addLeadData.leaderid = leadid;
      this.addDutyData.deptName = deptname;
      this.addDutyData.leadername = leadname;
      this.addDutyData.leaderphone = deptphone;
    },
    // 
    addDutySubmit: function() {
      this.$refs.addDutyData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addDutyData);
          this.dutysData.push(para);
          this.$refs["addDutyData"].resetFields();
          this.addDutyVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    editDutySubmit: function() {
      this.$refs.addDutyData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addDutyData);
          this.dutysData.splice(this.tindex, 1, para);
          this.$refs["addDutyData"].resetFields();
          this.addDutyVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    handleDelete3(index, row) {
      this.dutysData.splice(index, 1);
      this.$refs["addDutyData"].resetFields();
      this.addDutyVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },

    //  --------------
    projectSubmitAll: function() {
      // this.$refs.basicInfo.validate(valid => {
      //   if (valid) {
      var str = "";
      for (var i = 0; i < this.basicInfo.streetids.length; i++) {
        str += this.basicInfo.streetids[i] + ",";
      }
      this.basicInfo.streetids = str;
      if (this.basicInfo.streetids.length > 1) {
        str = str.substr(0, str.length - 1);
      }

      // id
      if (this.basicInfo.arealeadids.length == 0) {
        this.basicInfo.arealeadids = "";
      } else {
        if (this.basicInfo.arealeadids.length == 1) {
          this.basicInfo.arealeadids = this.basicInfo.arealeadids[0] + ",";
        } else {
          this.basicInfo.arealeadids = this.basicInfo.arealeadids.join(",");
        }
      }
      let para = this.basicInfo;
      para.leaderList = this.leadsData;
      para.dutyList = this.dutysData;
      para.ruleList = this.ruleList;
      para.id = this.projectId;
      para = JSON.stringify(para);

      editProject(para, res => {
        let cpara = { pid: this.projectId };
        changeFormstate(cpara, data => {
          synchFormstate({ pid: this.projectId }, ress => {
            this.$emit("my-event");
            this.$emit("mykey-event");
            this.$message({
              message: "",
              type: "success"
            });
          });
        });
      });
      // } else {
      //   return false;
      // }
      // });
    }
  }
};
</script>

<style lang="scss">
.addproject {
  padding-top: 20px;
  .speed {
    .pbox {
      display: flex;
      margin-bottom: 10px;
      .tt {
        color: #333;
        font-size: 14px;
        width: 110px;
        text-align: right;
        line-height: 2;
        padding-top: 3px;
        padding-right: 5px;
      }
      .conbox {
        flex: 1;
        color: #333;
        font-size: 14px;
        line-height: 2;
        border: 1px solid #dcdfe6;
        padding: 3px 10px;
        border-radius: 3px;
        min-height: 64px;
      }
    }
  }
  .sxtitle {
    // overflow: hidden;
    // text-overflow: ellipsis;
    // white-space: nowrap;
    // cursor: pointer;
    text-align: left;
  }
  .addcontent {
    overflow-y: auto;
    overflow-x: hidden;
    padding: 8px;
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.1);
  }
  .back {
    margin-bottom: 20px;
  }
  .jiabtn {
    height: 36px;
    line-height: 36px;
    position: relative;
    .icon {
      color: #666;
      font-size: 24px;
      display: inline-block;
      height: 36px;
      line-height: 36px;
      padding: 0 10px;
      cursor: pointer;
    }
    .awrap {
      position: absolute;
      left: 44px;
      top: 0;
      border: 1px solid #ccc;
      background: #fff;
      width: 250px;
      padding: 5px 10px 10px;
      z-index: 99;
      p {
        color: #666;
      }
      .el-textarea {
        width: 100%;
        margin-bottom: 10px;
      }
      .buttons {
        display: flex;
        justify-content: center;
        .el-button {
          padding: 8px 18px;
        }
      }
    }
  }
  .box {
    margin-bottom: 30px;
    .boxcontent {
      padding: 20px;
      .addbtn {
        margin-bottom: 20px;
      }
    }
    .detail-basic2 {
      .el-form-item {
        margin-bottom: 18px;
      }
      .el-select {
        display: block;
      }
    }
    .basicbox {
      .blist {
        display: flex;
        .el-form-item {
          flex: 1;
          display: flex;
          .el-form-item__content {
            flex: 1;
            .el-select {
              width: 100%;
            }
          }
        }
      }
    }
  }
  .task-dialog {
    .taskbox {
      display: flex;
      .day {
        .el-input__inner {
          width: 80px;
          border-radius: 4px;
        }
        .el-input-group__append {
          padding: 0 8px;
          background: #fff;
          border: 0;
        }
      }
    }
    .leabox {
      .el-form-item {
        width: 100%;
      }
    }
  }
  .task-dialog2 {
    .el-form--inline .el-form-item__content {
      width: 260px;
      .el-select {
        width: 100%;
      }
    }
    .leabox2 {
      .el-form-item__content {
        width: 634px;
      }
    }
  }
  .goback {
    padding: 30px 0 10px;
    display: flex;
    justify-content: flex-end;
    .el-button {
      padding: 10px 30px;
    }
    .el-button + .el-button {
      margin-left: 20px;
    }
  }
}
.filelist {
  padding: 0 30px;
  .tt {
    color: #222;
    font-size: 14px;
    margin-bottom: 10px;
  }
  .list {
    a {
      display: block;
      line-height: 2;
      font-size: 14px;
      color: #1890ff;
    }
    .not {
      margin-top: 15px;
      font-weight: normal;
    }
  }
}
// 
.rule-dialog {
  .el-input-group__append,
  .el-input-group__prepend {
    color: #222;
    background: transparent;
    border: 0;
    padding: 0 5px;
  }
  .el-form-item {
    .el-input {
      width: 220px;
    }
  }
  .day {
    .el-input {
      width: 150px;
    }
  }
}
</style>



